import fileReader from './assets/fileReader'

fileReader(document.getElementById('file'), {
  readAs: 'dataurl',
  zip: true,
  zipWidth: 1,
  zipQuality: 0.7,
  success: (result, resultHandle) => {
    console.log(result.length)
    document.getElementById('fileShow').src = result[0]
  }
})



